import React, { useState, useRef } from 'react';
import { Box } from '@mui/material';

export default function StateForLearn() {
  // useState   
  const [count, setCount] = useState(0);
  
  // useRef   
  const renderCount = useRef(0);

  // useEffect    
  React.useEffect(() => {
    renderCount.current = renderCount.current + 1;
  });

  //    useState   
  const handleIncrementState = () => {
    setCount(count + 1);
  };

  //    useRef   
  const handleIncrementRef = () => {
    renderCount.current = renderCount.current + 1;
    console.log('useRef value updated:', renderCount.current);
  };

  return (
    <Box m={2}>
    <div>
      <h1>React useState useRef </h1>

      <div>
        <h2>useState  </h2>
        <p>State  : {count}</p>
        <button onClick={handleIncrementState}>useState  </button>
      </div>

      <div>
        <h2>useRef  </h2>
        <p> : {renderCount.current}</p>
        <button onClick={handleIncrementRef}>useRef   (  )</button>
      </div>

      <div>
        <h2>useState useRef </h2>
        <p><strong>useState:</strong>     .</p>
        <p><strong>useRef:</strong>     .</p>
      </div>
    </div>
    </Box>
  );
}